import NotesClient from './Notes.client';

interface Props {
  params: { slug?: string[] | string };
  searchParams?: Record<string, string | string[] | undefined>;
}

export default function FilterPage({ params, searchParams }: Props) {
  const rawSlug = params?.slug;
  const slugArr = Array.isArray(rawSlug) ? rawSlug : rawSlug ? [rawSlug] : [];

  const rawTag = slugArr[0];
  const tag = rawTag === 'all' ? undefined : rawTag;

  const page = Number(searchParams?.page ?? 1) || 1;
  const search = typeof searchParams?.search === 'string' ? searchParams.search : '';

  return <NotesClient key={tag ?? 'all'} tag={tag} initialPage={page} initialSearch={search} />;
}
